




















































































.github {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: #868686;
  font-weight: bold;
  transition: .2s;
  &:hover {
    color: #333;
  }
}

.github__icon {
  font-size: 1.5rem;
}

.github__inner {
  margin-left: 7px;
  .github__name {
    font-size: .8rem;
    color: #555;
  }
  .github__info {
    display: flex;
    align-items: center;
    justify-content: center;
    > span {
      font-size: .7rem;
      color: #868686;
    }
    .github__message {
      font-size: .7rem;
    }
    .github__dot {
      border: 1px solid #777;
      margin: 0 10px;
      border-radius: 50%;
    }
  }
}

.white.github {
  color: rgba(255, 255, 255, .8);
  &:hover {
    color: #fff;
  }
  .github__name {
    color: rgba(255, 255, 255, .8);
  }
  .github__info > span {
    color: rgba(255, 255, 255, .8);
  }
  .github__dot {
    border-color: rgba(255, 255, 255, .9);
  }
}
